.modal-container {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.3);
}

.movie-details-container-closed {
  display: none;
}

.movie-details-container {
  padding: 1rem 1rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: rgba(0, 0, 0, 0.3);
  border: 0.5px solid #353c44;
  border-radius: 0.5rem;
  backdrop-filter: blur(10px);
  box-shadow: inset 0 0 30px rgb(255 255 255 / 10%), -5px 20px 30px #2b2b2ba3;
  z-index: 5;
  position: relative;
}
.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal-close > button {
  cursor: pointer;
  font-size: 1.5rem;
  font-weight: 600;
  color: #fff;
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  box-shadow: inset 0 0 30px rgb(255 255 255 / 10%), -5px 20px 30px #2b2b2ba3;
}
.modal-close > button:hover {
  color: #9999;
}
.movie-poster img {
  width: 250px;
  margin: 1rem 1rem;
  border-radius: 0.5rem;
}

.modal-movie-title {
  font-size: 2rem;
}
.movie-info {
  max-width: 500px;
  color: #eee;
}
.modal-genres {
  display: flex;
  flex-direction: row;
  margin-bottom: 1rem;
}
.modal-genres p {
  padding: 3px 5px;
  margin-right: 10px;
  background: #ccc;
  color: #000;
  border-radius: 0.5rem;
}

.modal-overview {
  font-weight: 600;
}

.modal-sub-info {
  max-width: 250px;
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}
.modal-sub-info span {
  color: #fff;
  margin-top: 1rem;
  padding: 3px 5px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  box-shadow: inset 0 0 30px rgb(255 255 255 / 10%), -5px 20px 30px #2b2b2ba3;
}

@media (max-width: 700px) {
  .movie-poster img {
    width: 200px;
  }
  .movie-details-container {
    flex-direction: column;
  }
  .movie-info {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .modal-sub-info {
    display: none;
  }
}
