.upcoming-movie-list {
  margin-top: 1rem;
  margin-bottom: 3rem;
  padding: 0 1rem;
  padding-top: 3rem;
  padding-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(0, 0, 0, 0.3);
  border: 0.5px solid #353c44;
  border-radius: 0.5rem;
  backdrop-filter: blur(10px);
  box-shadow: inset 0 0 30px rgb(255 255 255 / 10%), -5px 20px 30px #2b2b2ba3;
  z-index: 0;
}

.upcoming-movie-list h2 {
  position: absolute;
  top: 10px;
  left: 16px;
  color: #eeeeee;
}
