.navbar__nav-container {
  margin-top: 1rem;
  margin-bottom: 2rem;
  padding: 0.7rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(0, 0, 0, 0.3);
  border: 0.5px solid #353c44;
  border-radius: 0.7rem;
  backdrop-filter: blur(10px);
  box-shadow: inset 0 0 30px rgb(255 255 255 / 10%), -5px 20px 30px #2b2b2ba3;
}

.navbar__nav-logo {
  padding: 0.5rem;
}

.navbar__nav-logo h2 > a {
  text-decoration: none;
  background: linear-gradient(
    to right,
    #072243,
    #c49882
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.7;
  font-weight: 800;
  cursor: pointer;
}

.searchBar {
  padding: 0.5rem 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.searchBar {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  border: 0.5px solid rgba(255, 255, 255, 0.2);
}

.searchBar input[type="text"] {
  color: white;
  padding-left: 1rem;
  border: none;
  background-color: transparent;
}

.searchBar input[type="text"]:focus {
  outline: none;
}

.navbar__nav-search .icons8-search {
  display: inline-block;
  width: 1.3rem;
  height: 1.3rem;
  background: url("data:image/svg+xml;base64,")
    50% 50% no-repeat;
  background-size: 100%;
}

.icons8-home {
  display: inline-block;
  width: 1.3rem;
  height: 1.3rem;
  background: url("data:image/svg+xml;base64,")
    50% 50% no-repeat;
  background-size: 100%;
}

@media (max-width: 500px) {
  .navbar__nav-logo {
    display: none;
  }
  .navbar__nav-container {
    justify-content: center;
    margin-bottom: 0;
  }
  .searchBar {
    padding: 5px;
  }
}
