.card-main-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: nowrap;
  overflow-x: auto;
}

.card-container {
  border: 0.5px solid #353c44;
  margin-right: 1rem;
  padding: 0.2rem;
  margin-bottom: 1rem;
  display: inline-block;
  border-radius: 0.5rem;
  background: linear-gradient(
    to top,
    #072243,
    #c49882
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.card-main-container::-webkit-scrollbar {
  height: 0.6rem;
}

.card-main-container::-webkit-scrollbar-track {
  background-color: #8d8d8d;
  border-radius: 0.3rem;
}

.card-main-container::-webkit-scrollbar-thumb {
  background-color: #e4e4e4;
  border-radius: 0.3rem;
}

.card {
  height: 290px;
  position: relative;
}

.card img {
  width: 150px;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: 250ms ease;
}
.card img:hover {
  opacity: 80%;
}
.card-rating {
  padding: 1px 3px;
  color: #bcb4c1;
  border-radius: 2px;
  position: absolute;
  bottom: 0px;
  right: 2px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #072243;
  border: 0.5px solid #353c44;
  transform: scale(0.7);
}

.card-rating p {
  padding-top: 3px;
  padding-left: 2px;
  padding-right: 2px;
  margin-left: 5px;
}

.card-info {
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: start;
  inline-size: 140px;
  overflow-wrap: break-word;
  hyphens: manual;
  white-space: nowrap;
  overflow: hidden;
  display: inline-block;
}
.card-info p {
  overflow: hidden;
  text-overflow: ellipsis;
}

.card-info p a {
  text-decoration: none;
  font-weight: 500;
  color: white;
}

.card-release {
  color: #eee;
  font-weight: 300;
  position: absolute;
  bottom: 2px;
  left: -5px;
  transform: scale(0.7);
}

.add-more {
  cursor: pointer;
  max-width: 100px;
  color: #eee;
  padding: 1rem 2rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  border: 0.5px solid rgba(255, 255, 255, 0.2);
}
